// src/pages/community/[type]/[id].tsx
import React, {useState, useEffect} from 'react';
import {useRouter} from 'next/router';
import Layout from '@/component/layout/MainLayout';
import styles from '@/styles/community/PostDetail.module.css';
import {
  getConcernDetail,
  getProjectDetail,
  getStudyDetail,
  deleteConcern,
  deleteProject,
  deleteStudy,
} from '@/api/communityService/community';
import {toggleLike, checkLike, getLikeCount} from '@/api/communityService/like';
import {
  getComments,
  createComment,
  getCommentCount,
  updateComment,
  deleteComment
} from '@/api/communityService/comment';
import {getTags} from '@/api/communityService/tag';
import {
  PostDetailResponse,
  CommunityPostType,
  CommentResponse,
  CreateCommentRequest,
  UpdateCommentRequest // ‚≠êÔ∏è [ÏàòÏ†ï] ÎåìÍ∏Ä ÏàòÏ†ïÏùÑ ÏúÑÌïú ÏöîÏ≤≠ ÌÉÄÏûÖÏù¥ ÏûàÎã§Í≥† Í∞ÄÏ†ï
} from '@/types/communityService/communityType';

export default function PostDetail() {
  const router = useRouter();
  const {type, id} = router.query;

  const [post, setPost] = useState<PostDetailResponse | null>(null);
  const [comments, setComments] = useState<CommentResponse[]>([]);
  const [tags, setTags] = useState<string[]>([]);
  const [likeCount, setLikeCount] = useState(0);
  const [commentCount, setCommentCount] = useState(0);
  const [isLiked, setIsLiked] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [userSignId, setUserSignId] = useState<string>('');
  const [commentContent, setCommentContent] = useState('');
  const [replyTo, setReplyTo] = useState<number | null>(null);
  const [replyContent, setReplyContent] = useState('');

  // ‚≠êÔ∏è [Ï∂îÍ∞Ä] ÎåìÍ∏Ä ÏàòÏ†ï ÏÉÅÌÉú ‚≠êÔ∏è
  const [editingCommentId, setEditingCommentId] = useState<number | null>(null);
  const [editedContent, setEditedContent] = useState('');

  useEffect(() => {
    setUserSignId(localStorage.getItem('userSignId') || '');
  }, []);

  useEffect(() => {
    if (type && id) {
      loadPostDetail();
    }
  }, [type, id, userSignId]);

  const loadPostDetail = async () => {
    if (!type || !id) return;

    setIsLoading(true);
    try {
      const communityId = Number(id);
      const postType = (type as string).toUpperCase() as CommunityPostType;

      let postData: PostDetailResponse;

      switch (postType) {
        case CommunityPostType.CONCERN:
          postData = await getConcernDetail(communityId, userSignId);
          break;
        case CommunityPostType.PROJECT:
          postData = await getProjectDetail(communityId, userSignId);
          break;
        case CommunityPostType.STUDY:
          postData = await getStudyDetail(communityId, userSignId);
          break;
        default:
          throw new Error('Invalid post type');
      }

      setPost(postData);

      // Ï¢ãÏïÑÏöî ÏÉÅÌÉúÏôÄ Í∞úÏàò Î°úÎìú
      // üö® [ÏóêÎü¨ ÏàòÏ†ï 1] getCommentCountÏùò Ïù∏Ïûê ÏàúÏÑú ÏàòÏ†ï: (type, communityId)Í∞Ä ÏïÑÎãå (communityId, type) ÏàúÏÑúÏòÄÏäµÎãàÎã§.
      const [liked, count, tagsData, commentsData, countData] = await Promise.all([
        checkLike(postType, communityId, userSignId),
        getLikeCount(postType, communityId),
        getTags(communityId),
        getComments(postType, communityId),
        getCommentCount(postType, communityId), // üëà Ïù∏Ïûê ÏàúÏÑú Î≥ÄÍ≤Ω
      ]);

      setIsLiked(liked);
      setLikeCount(count);
      setTags(tagsData);
      setComments(commentsData);
      setCommentCount(countData);

    } catch (error) {
      console.error('Failed to load post detail:', error);
      setPost(null);
    } finally {
      setIsLoading(false);
    }
  };

  const handleLikeToggle = async () => {
    if (!userSignId || !post || !type || !id) {
      alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      return;
    }

    try {
      const postType = (type as string).toUpperCase() as CommunityPostType;
      await toggleLike(postType, Number(id), userSignId);
      setIsLiked(prev => !prev);
      setLikeCount(prev => (isLiked ? prev - 1 : prev + 1));
    } catch (error) {
      alert('Ï¢ãÏïÑÏöî Ï≤òÎ¶¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
    }
  };

  const handleCommentSubmit = async () => {
    if (!userSignId) {
      alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      return;
    }
    if (!commentContent.trim()) {
      alert('ÎåìÍ∏Ä ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }
    if (!type || !id) return;

    try {
      const postType = (type as string).toUpperCase() as CommunityPostType;
      const communityId = Number(id);
      const request: CreateCommentRequest = {content: commentContent};
      await createComment(postType, communityId, request, userSignId);
      setCommentContent('');

      // ÎåìÍ∏Ä Î™©Î°ù Î∞è Ïπ¥Ïö¥Ìä∏ ÏÉàÎ°úÍ≥†Ïπ®
      const [updatedComments, countData] = await Promise.all([
        getComments(postType, communityId),
        getCommentCount(postType, communityId), // üëà Ïù∏Ïûê ÏàúÏÑú Î≥ÄÍ≤Ω
      ]);
      setComments(updatedComments);
      setCommentCount(countData);

    } catch (error) {
      alert('ÎåìÍ∏Ä ÏûëÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      console.error('Create comment failed:', error);
    }
  };

  const handleReplySubmit = async (parentCommentId: number) => {
    if (!userSignId) {
      alert('Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.');
      return;
    }
    if (!replyContent.trim()) {
      alert('ÎãµÍ∏Ä ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }
    if (!type || !id) return;

    try {
      const postType = (type as string).toUpperCase() as CommunityPostType;
      const communityId = Number(id);
      const request: CreateCommentRequest = {
        content: replyContent,
        parentCommentId: parentCommentId,
      };
      await createComment(postType, communityId, request, userSignId);
      setReplyContent('');
      setReplyTo(null);

      // ÎåìÍ∏Ä Î™©Î°ù Î∞è Ïπ¥Ïö¥Ìä∏ ÏÉàÎ°úÍ≥†Ïπ®
      const [updatedComments, countData] = await Promise.all([
        getComments(postType, communityId),
        getCommentCount(postType, communityId), // üëà Ïù∏Ïûê ÏàúÏÑú Î≥ÄÍ≤Ω
      ]);
      setComments(updatedComments);
      setCommentCount(countData);

    } catch (error) {
      alert('ÎãµÍ∏Ä ÏûëÏÑ±Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      console.error('Create reply failed:', error);
    }
  };

  // ‚≠êÔ∏è [Ï∂îÍ∞Ä] ÎåìÍ∏Ä ÏÇ≠Ï†ú Ìï∏Îì§Îü¨ ‚≠êÔ∏è
  const handleDeleteComment = async (commentId: number) => {
    if (!confirm('Ï†ïÎßêÎ°ú Ïù¥ ÎåìÍ∏ÄÏùÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) return;
    if (!type || !id || !userSignId) return;

    try {
      const postType = (type as string).toUpperCase() as CommunityPostType;
      const communityId = Number(id);

      // üö® ÎåìÍ∏Ä ÏÇ≠Ï†ú API Ìò∏Ï∂ú
      await deleteComment(postType, communityId, commentId, userSignId);

      // ÏÇ≠Ï†ú ÌõÑ ÎåìÍ∏Ä Î™©Î°ù Î∞è Ïπ¥Ïö¥Ìä∏ ÏÉàÎ°úÍ≥†Ïπ®
      const [updatedComments, countData] = await Promise.all([
        getComments(postType, communityId),
        getCommentCount(postType, communityId), // üëà Ïù∏Ïûê ÏàúÏÑú Î≥ÄÍ≤Ω
      ]);
      setComments(updatedComments);
      setCommentCount(countData);

    } catch (error) {
      alert('ÎåìÍ∏Ä ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. (ÏûêÏã†Ïùò ÎåìÍ∏ÄÎßå ÏÇ≠Ï†úÌï† Ïàò ÏûàÏäµÎãàÎã§)');
      console.error('Delete comment failed:', error);
    }
  };

  // ‚≠êÔ∏è [Ï∂îÍ∞Ä] ÎåìÍ∏Ä ÏàòÏ†ï Ìï∏Îì§Îü¨ ‚≠êÔ∏è
  const handleEditComment = async (commentId: number) => {
    if (!editedContent.trim()) {
      alert('ÏàòÏ†ïÌï† ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }
    if (!type || !id || !userSignId) return;

    try {
      const postType = (type as string).toUpperCase() as CommunityPostType;
      const communityId = Number(id);
      const request: UpdateCommentRequest = { // UpdateCommentRequest ÏÇ¨Ïö© Í∞ÄÏ†ï
        content: editedContent
      };

      // üö® ÎåìÍ∏Ä ÏàòÏ†ï API Ìò∏Ï∂ú
      await updateComment(postType, communityId, commentId, request, userSignId);

      setEditingCommentId(null);
      setEditedContent('');

      // ÏàòÏ†ï ÌõÑ ÎåìÍ∏Ä Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ®
      const updatedComments = await getComments(postType, communityId);
      setComments(updatedComments);

    } catch (error) {
      alert('ÎåìÍ∏Ä ÏàòÏ†ïÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. (ÏûêÏã†Ïùò ÎåìÍ∏ÄÎßå ÏàòÏ†ïÌï† Ïàò ÏûàÏäµÎãàÎã§)');
      console.error('Edit comment failed:', error);
    }
  };

  const formatTime = (isoString: string) => {
    // Í∞ÑÎã®Ìïú ÏãúÍ∞Ñ Ìè¨Îß∑ÌåÖ Î°úÏßÅ
    const date = new Date(isoString);
    const now = new Date();
    const diff = now.getTime() - date.getTime();

    if (diff < 60000) return 'Î∞©Í∏à Ï†Ñ'; // 1Î∂Ñ ÎØ∏Îßå
    if (diff < 3600000) return `${Math.floor(diff / 60000)}Î∂Ñ Ï†Ñ`; // 1ÏãúÍ∞Ñ ÎØ∏Îßå
    if (diff < 86400000) return `${Math.floor(diff / 3600000)}ÏãúÍ∞Ñ Ï†Ñ`; // 24ÏãúÍ∞Ñ ÎØ∏Îßå

    return date.toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
    }).replace(/\. /g, '.').replace(/\.$/, '').replace(' ', ' ');
  };
  const renderComments = (comments: CommentResponse[]) => {
    // ÎîîÎ≤ÑÍπÖÏö©: ÏÑúÎ≤ÑÏóêÏÑú Ïñ¥Îñ§ Îç∞Ïù¥ÌÑ∞Í∞Ä Ïò§ÎäîÏßÄ ÌôïÏù∏ (ÎÇòÏ§ëÏóê Î∞∞Ìè¨ Ïãú ÏÇ≠Ï†ú Í∞ÄÎä•)
    console.log('[ÎåìÍ∏Ä Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞]', comments);

    return comments.map(comment => {
      // Í∞úÎ≥Ñ ÎåìÍ∏Ä Î°úÍ∑∏ (ÎåÄÎåìÍ∏Ä Ìè¨Ìï®)
      console.log('[Í∞úÎ≥Ñ ÎåìÍ∏Ä]', {
        commentId: comment.commentId,
        userId: comment.userId,
        authorNickname: comment.authorNickname,
        content: comment.content,
        isDeleted: comment.isDeleted,
        parentCommentId: comment.parentCommentId,
      });

      // ÏûëÏÑ±Ïûê ÌëúÏãú Î°úÏßÅ: ÎãâÎÑ§ÏûÑ ÏûàÏúºÎ©¥ ÎãâÎÑ§ÏûÑ ‚Üí authorId ‚Üí ÏùµÎ™Ö
      const displayName = comment.authorNickname
          ? comment.authorNickname
          : comment.userId
              ? comment.userId
              : 'ÏùµÎ™Ö';

      return (
          <div
              key={comment.commentId}
              className={comment.parentCommentId === null ? styles.comment : styles.reply}
          >
            {editingCommentId === comment.commentId ? (
                // ÏàòÏ†ï Ìèº
                <div className={styles.replyInput}>
              <textarea
                  value={editedContent}
                  onChange={(e) => setEditedContent(e.target.value)}
                  placeholder="ÎåìÍ∏ÄÏùÑ ÏàòÏ†ïÌïòÏÑ∏Ïöî..."
                  className={styles.textarea}
              />
                  <div className={styles.replyButtons}>
                    <button onClick={() => setEditingCommentId(null)} className={styles.cancelButton}>
                      Ï∑®ÏÜå
                    </button>
                    <button
                        onClick={() => handleEditComment(comment.commentId)}
                        className={styles.submitButton}
                    >
                      ÏàòÏ†ï ÏôÑÎ£å
                    </button>
                  </div>
                </div>
            ) : (
                <>
                  <div className={styles.commentHeader}>
                <span className={styles.commentAuthor}>
                  {comment.isDeleted ? 'ÏÇ≠Ï†úÎêú ÏÇ¨Ïö©Ïûê' : displayName}
                </span>
                    <span className={styles.commentTime}>{formatTime(comment.createdAt)}</span>

                    {/* Î≥∏Ïù∏ ÎåìÍ∏ÄÏù∏ÏßÄ ÌôïÏù∏ ‚Üí ÏàòÏ†ï/ÏÇ≠Ï†ú Î≤ÑÌäº ÎÖ∏Ï∂ú */}
                    {comment.userId === userSignId && !comment.isDeleted && (
                        <div className={styles.commentActions}>
                          <button
                              onClick={() => {
                                setEditingCommentId(comment.commentId);
                                setEditedContent(comment.content);
                              }}
                              className={styles.actionButton}
                          >
                            ÏàòÏ†ï
                          </button>
                          <button
                              onClick={() => handleDeleteComment(comment.commentId)}
                              className={styles.actionButton}
                          >
                            ÏÇ≠Ï†ú
                          </button>
                        </div>
                    )}
                  </div>

                  <p className={styles.commentContent}>
                    {comment.isDeleted ? 'ÏÇ≠Ï†úÎêú ÎåìÍ∏ÄÏûÖÎãàÎã§.' : comment.content}
                  </p>

                  {/* ÎãµÍ∏Ä Îã¨Í∏∞ Î≤ÑÌäº */}
                  {!comment.isDeleted && (
                      <button
                          className={styles.replyButton}
                          onClick={() => {
                            setReplyTo(comment.commentId);
                            setReplyContent('');
                          }}
                      >
                        ÎãµÍ∏Ä
                      </button>
                  )}
                </>
            )}

            {/* ÎãµÍ∏Ä ÏûÖÎ†• Ìèº */}
            {replyTo === comment.commentId && (
                <div className={styles.replyInput}>
              <textarea
                  value={replyContent}
                  onChange={(e) => setReplyContent(e.target.value)}
                  placeholder="ÎãµÍ∏ÄÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
                  className={styles.textarea}
              />
                  <div className={styles.replyButtons}>
                    <button onClick={() => setReplyTo(null)} className={styles.cancelButton}>
                      Ï∑®ÏÜå
                    </button>
                    <button
                        onClick={() => handleReplySubmit(comment.commentId)}
                        className={styles.submitButton}
                    >
                      ÏûëÏÑ±
                    </button>
                  </div>
                </div>
            )}

            {/* ÎåÄÎåìÍ∏Ä Ïû¨Í∑Ä Î†åÎçîÎßÅ */}
            {comment.replies && comment.replies.length > 0 && (
                <div className={styles.repliesContainer}>
                  {renderComments(comment.replies)}
                </div>
            )}
          </div>
      );
    });
  };
  const handlePostDelete = async () => {
    if (!post || !type || !id) return;
    if (!confirm('Ï†ïÎßêÎ°ú Í≤åÏãúÍ∏ÄÏùÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) return;

    try {
      const communityId = Number(id);
      const postType = (type as string).toUpperCase() as CommunityPostType;

      switch (postType) {
        case CommunityPostType.CONCERN:
          await deleteConcern(communityId, userSignId);
          break;
        case CommunityPostType.PROJECT:
          await deleteProject(communityId, userSignId);
          break;
        case CommunityPostType.STUDY:
          await deleteStudy(communityId, userSignId);
          break;
        default:
          throw new Error('Invalid post type');
      }

      alert('Í≤åÏãúÍ∏ÄÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.');
      router.push('/community');
    } catch (error) {
      alert('Í≤åÏãúÍ∏Ä ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. (ÏûëÏÑ±ÏûêÎßå ÏÇ≠Ï†úÌï† Ïàò ÏûàÏäµÎãàÎã§)');
      console.error('Delete post failed:', error);
    }
  };

  const handlePostEdit = () => {
    if (!post || !type || !id) return;
    router.push(`/community/write?type=${type}&id=${id}`);
  };


  if (isLoading) {
    return (
        <Layout>
          <div className={styles.loading}>Î°úÎî© Ï§ë...</div>
        </Layout>
    );
  }

  if (!post) {
    return (
        <Layout>
          <div className={styles.error}>Í≤åÏãúÍ∏ÄÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.</div>
        </Layout>
    );
  }

  const badgeClass = {
    [CommunityPostType.CONCERN]: styles.badgeConcern,
    [CommunityPostType.PROJECT]: styles.badgeProject,
    [CommunityPostType.STUDY]: styles.badgeStudy,
  }[post.postType!];

  const postTypeLabel = {
    [CommunityPostType.CONCERN]: 'Í≥†ÎØº ÏÉÅÎã¥',
    [CommunityPostType.PROJECT]: 'ÌîÑÎ°úÏ†ùÌä∏',
    [CommunityPostType.STUDY]: 'Ïä§ÌÑ∞Îîî',
  }[post.postType!];

  return (
      <Layout>
        <div className={styles.container}>
          <div className={styles.post}>
            <div className={styles.postHeader}>
              <span className={`${styles.badge} ${badgeClass}`}>{postTypeLabel}</span>
              {post.userId === userSignId && (
                  <div className={styles.actions}>
                    <button onClick={handlePostEdit} className={styles.editButton}>
                      ÏàòÏ†ï
                    </button>
                    <button onClick={handlePostDelete} className={styles.deleteButton}>
                      ÏÇ≠Ï†ú
                    </button>
                  </div>
              )}
            </div>

            <h1 className={styles.title}>{post.title}</h1>

            <div className={styles.authorInfo}>
              <span className={styles.author}>{post.authorNickname || 'ÏùµÎ™Ö'}</span>
              <span className={styles.time}>{formatTime(post.createdAt)}</span>
            </div>

            <div className={styles.tags}>
              {tags.map((tag) => (
                  <span key={tag} className={styles.tag}>
                    #{tag}
                  </span>
              ))}
            </div>

            <div
                className={styles.content}
                dangerouslySetInnerHTML={{__html: post.content}}
            />

            <div className={styles.stats}>
              <button
                  onClick={handleLikeToggle}
                  className={`${styles.likeButton} ${isLiked ? styles.liked : ''}`}
              >
                ‚ù§Ô∏è Ï¢ãÏïÑÏöî {likeCount}
              </button>
              <span className={styles.commentStat}>üí¨ ÎåìÍ∏Ä {commentCount}</span>
            </div>
          </div>

          <div className={styles.commentsSection}>
            <h2 className={styles.commentsTitle}>ÎåìÍ∏Ä {commentCount}</h2>

            {userSignId && (
                <div className={styles.commentInput}>
              <textarea
                  value={commentContent}
                  onChange={(e) => setCommentContent(e.target.value)}
                  placeholder="ÎåìÍ∏ÄÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî..."
                  className={styles.textarea}
              />
                  <button
                      onClick={handleCommentSubmit}
                      className={styles.submitButton}
                  >
                    ÏûëÏÑ±
                  </button>
                </div>
            )}

            <div className={styles.commentsList}>
              {renderComments(comments)}
            </div>
          </div>

          <button
              onClick={() => router.push('/community')}
              className={styles.backButton}
          >
            Î™©Î°ùÏúºÎ°ú
          </button>
        </div>
      </Layout>
  );
}